import { Button, Checkbox, Form, Input, Select } from 'antd';
import {RightOutlined} from '@ant-design/icons';
import Base from "@/components/layout/base";
import isMobilePhone from '@/utils/validator/isMobilePhone';
import isSmsCode from '@/utils/validator/isSmsCode';
import './index.scss';

const { Option } = Select;

interface RegisterFormData {
  interCode: string,
  mobile: string,
  smsCode: string,
  policy?: boolean
}

const onFinish = (values: RegisterFormData) => {
  const {policy, ...formData} = values;
  console.log('Success:', policy, formData);
};

const selectBefore = (
  <Form.Item 
    noStyle
    name="interCode">
    <Select 
      popupMatchSelectWidth={false} 
      bordered={false} 
      onClick={(event) => {event.stopPropagation()}} 
      optionLabelProp="label"
    >
      <Option value="86" label="+86">+86()</Option>
      <Option value="65" label="+65">+65()</Option>
      <Option value="852" label="+852">+852()</Option>
      <Option value="853" label="+853">+853()</Option>
    </Select>
  </Form.Item>
);

function Register() {
  const [ form ] = Form.useForm();

  return (
    <Base>
      <div className="base-register">
        <div className="base-title"></div>
        <Form
          form={form}
          name="base-form-register"
          initialValues={{ interCode: 86 }}
          onFinish={onFinish}
          autoComplete="off"
          colon={false}
          className='base-form'
        >
          <Form.Item
            label=""
            name="mobile"
            rules={[
              { validator: (_rule, value) => {
                const interCode = form.getFieldValue('interCode');
                if(!value) {
                  return Promise.reject(new Error(''));
                } else if(isMobilePhone(value, interCode)) {
                  return Promise.resolve();
                } else {
                  return Promise.reject(new Error(''));
                }
              }}
            ]}
          >
            <Input prefix={selectBefore} placeholder='' />
          </Form.Item>

          <Form.Item 
            className='sms-code-item'
            label="">
            <Form.Item 
              name="smsCode" 
              noStyle 
              rules={[
                { validator: (_rule, value) => {
                  if(!value) {
                    return Promise.reject(new Error(''));
                  }else if(isSmsCode(value)) {
                    return Promise.resolve();
                  } else {
                    return Promise.reject(new Error(''));
                  }
                }}
              ]} 
              className='sms-code-item2'>
              <Input placeholder=''/> 
            </Form.Item>
            
            <Button className='sms-code-btn' type="link" size='small' style={{padding: 0}}>
            
            </Button>
          
          </Form.Item>

          <Form.Item name="policy" valuePropName="checked">
            <Checkbox className='policy'> <a href="https://page.meituan.net/html/1615180237352_38ceb3/index.html" target="_blank" ></a></Checkbox>
          </Form.Item>

          <Form.Item>
            <Button type="primary" htmlType="submit" className='base-submit-btn'>
              
            </Button>
          </Form.Item>

          <Button type="link" style={{padding: 0, fontSize: 15}}>
           <RightOutlined size={18} />
          </Button>
        </Form>
      </div>
    </Base>
  )
}

export default Register;
